
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
Group,
XInput,
PopupPicker,
XAddress,
XTextarea,
XSwitch,
XHeader,
ViewBox,
XButton,
Toast
},
data() {
return {
title: '',
addressData: ChinaAddressData,
addressValue: [],
name: "",
phone: "",
detailed: "",
isDefault: false,
show: false,
showToast: false,
};
},
mounted() {
var _this = this;
if(this.$route.params["openEdit"] == "1") {
this.title = '';
this.isShow = false;
this.isHide = true;
var address_id = this.$route.params["address_id"];
var name = this.$route.params["name"];
var phone = this.$route.params["phone"];
var info = this.$route.params["info"];
var region = this.$route.params["region"];
this.name = name;
this.phone = phone;
this.detailed = info;
}
},
methods: {
getName(value) {
return value2name(value, ChinaAddressV4Data)
},

postInfo() {

var name = this.name;

var phone = this.phone;

var address = this.getName(this.addressValue);

var detailed = address + this.detailed;
if(name.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
}
if(phone.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
}
if(!this.isPhone(phone)) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
};
if(address.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
};
if(this.detailed.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
};
if(this.isDefault == true) {
var is_default = "1"
} else {
var is_default = "0"
}
this.$post(
"address/add", {
add: {
people: name,
phone: phone,
region: address,
info: detailed,
is_default: is_default
}
},
res => {
console.log(res);
if(res.res >= 1) {
this.showToast = true;
var _this = this;
setTimeout(function() {
_this.$router.back(-1);
}, 1000)
}
if(res < 0) {
this.$vux.toast.show({
text: ",",
type: "text",
position: "middle"
});
}

}
);
},

editInfo() {
if(this.$route.params["openEdit"] == "1") {
var address_id = this.$route.params["address_id"];
}

var name = this.name;

var phone = this.phone;

var address = this.getName(this.addressValue);

var detailed = address + this.detailed;
if(name.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
}
if(phone.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
}
if(!this.isPhone(phone)) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
};
if(address.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
};
if(this.detailed.length <= 0) {
this.$vux.toast.show({
text: "",
type: "text",
position: "middle"
});
return false;
};
if(this.isDefault == true) {
var is_default = "1"
} else {
var is_default = "0"
}
this.$post(
"address/save", {
save: {
people: name,
phone: phone,
region: address,
info: detailed,
is_default: is_default
},
address_id: address_id
},
res => {
console.log(res);
if(res.res >= 1) {
this.showToast = true;
var _this = this;
setTimeout(function() {
_this.$router.back(-1);
}, 1000)
}
if(res < 0) {
this.$vux.toast.show({
text: ",",
type: "text",
position: "middle"
});
}

}
);
},

isPhone(str) {
var reg = /^0?1[3|4|5|8][0-9]\d{8}$/;
return reg.test(str);
return true;
},
}
};



